<template>
   <div class="table-responsive" >
		<v-data-table
			:headers="headers"
			:items="messageList"
			:items-per-page="15"
			:search="search"
			align='center'
		>
		<template v-slot:item.paid="{ item }" >
				<v-btn small tile elevation="0" @click="changePaidStatus(item,item.invoiceId)" class="primary">{{item.paid}}</v-btn>
			</template>
			<template v-slot:item.status="{ item }" >
                <v-select class="text-xs-sm" @change="updateService(item.invoiceId, item.status)"  :items="Departments" 
							v-model="item.status" dense></v-select>
			</template>
			<template v-slot:item.action="{ item }" >
				<v-btn @click="openMessage(item)" text icon color="grey" class="mr-3"   small slot="activator">
					<v-icon class="primary--text">remove_red_eye</v-icon>
				</v-btn>
				<v-btn text icon color="grey" small  @click="deleteItemFromInvoicesList(item)" slot="activator">
					<v-icon class="error--text">delete</v-icon>
				</v-btn>
			</template>
			
		</v-data-table>
        	<v-dialog v-model="open" max-width="400">
		<v-card class="py-6 px-4">
			<!-- <h4>{{messageTitle}}</h4> -->
			<p>{{selectedMessage}}</p>
			<v-card-actions class="layout justify-center">
				<v-btn color="primary" @click="open = false">OK</v-btn>
			</v-card-actions>
		</v-card>
	</v-dialog>
		<emb-delete-confirmation-2
			ref="deleteConfirmationDialog"
			messageTitle="Are You Sure You Want To Delete?"
			messageDescription="Are you sure you want to delete this invoice permanently?"
			@onConfirm="ondeleteItemFromInvoicesList"
			btn1="Cancel"
			btn2="Yes"
		>
		</emb-delete-confirmation-2>
	</div>
</template>
<style  scoped>
#products{
	display: flex;
	justify-content: space-between;
	/* text-align: center; */
	align-items: center;
	margin-top: 10px;
}
#tittle{
	display: flex;
	justify-content: space-between;
	align-items: center;
}
/* #producr{
	display: flex;
	justify-content: space-between;
} */
</style>
<script>
import contact from "Api/contact";
import moment from "moment"
import { mapGetters } from "vuex";
export default {
	computed: {
			...mapGetters(["linksformbackend"])
		},
  props: {
    search: {
      default: null,
      type: String
    }
  },
  data() {
    return {
        selectedMessage: '',
        open:null,
		Departments: [
            'SENT',
            'RECEIVED',
            'CANCELED',
            'TECHNICIAN-ON-THE-WAY',
            'TECHNICIAN-AT-SITE',
            'JOB-DONE'
        ],
      editDialog: false,
      invoiceList: null,
      headers: [
		{
          text: "Date",
          sortable: false,
          value: "date"
        },
        // {
        //   text: "Invoice ID",
        //   sortable: false,
        //   value: "invoiceId"
		// },
		{
          text: "First Name",
          sortable: false,
          value: "firstName"
		},
		{
          text: "Last Name",
          sortable: false,
          value: "lastName"
        },
        {
          text: "Subject",
          sortable: false,
          value: "subject"
		},
		{
          text: "Reaad",
          sortable: false,
          value: "paid"
		},
		{
          text: "Action",
          sortable: false,
          value: "action"
        },
      ],
      selectDeletedItem: null,
	  messageList: [],
	selectProduts: [],
	pictures:'',
	name: '',
	total: ''
    };
  },
  async created() {
	// this.getInvoice();

	try {
        const res = await contact.getAllcontact()
        console.log(res.data.data)
		// console.log(res)
		res.data.data.forEach(el => {
			console.log(el)
			const dat = moment(el.updatedAt)
			const time = moment(el.updatedAt)
			const tt = time.format('LT')
			const da = dat.format('L');
			const date = tt + '' + ' '+ da
			this.messageList.push({
			  date: date,
		      invoiceId: el._id,
			  firstName: el.firstName,
			  lastName: el.lastName,
              paid: el.read,
              subject: el.subject,
			  status: el.status,
              email: el.email,
              message:el.message
			  })
			  console.log(el)
			  
			//   console.log()
		});
	} catch (err) {
		console.log(err)
	}
  },
  methods: {
    openMessage(item) {
        this.selectedMessage=item.message
        this.open=true
    },
    deleteItemFromInvoicesList(item) {
      this.$refs.deleteConfirmationDialog.openDialog();
      this.selectDeletedItem = item;
    },
    ondeleteItemFromInvoicesList() {
      this.$refs.deleteConfirmationDialog.close();
      let index = this.messageList.indexOf(this.selectDeletedItem);
      this.messageList.splice(index, 1);
    },
  }
};
</script>
